@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --background: 0 0% 100%;
    --foreground: 222.2 84% 4.9%;
    --card: 0 0% 100%;
    --card-foreground: 222.2 84% 4.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 222.2 84% 4.9%;
    --primary: 221.2 83.2% 53.3%;
    --primary-foreground: 210 40% 98%;
    --secondary: 210 40% 96%;
    --secondary-foreground: 222.2 84% 4.9%;
    --muted: 210 40% 96%;
    --muted-foreground: 215.4 16.3% 46.9%;
    --accent: 210 40% 96%;
    --accent-foreground: 222.2 84% 4.9%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 210 40% 98%;
    --border: 214.3 31.8% 91.4%;
    --input: 214.3 31.8% 91.4%;
    --ring: 221.2 83.2% 53.3%;
    --radius: 0.5rem;
  }

  .dark {
    --background: 222.2 84% 4.9%;
    --foreground: 210 40% 98%;
    --card: 222.2 84% 4.9%;
    --card-foreground: 210 40% 98%;
    --popover: 222.2 84% 4.9%;
    --popover-foreground: 210 40% 98%;
    --primary: 217.2 91.2% 59.8%;
    --primary-foreground: 222.2 84% 4.9%;
    --secondary: 217.2 32.6% 17.5%;
    --secondary-foreground: 210 40% 98%;
    --muted: 217.2 32.6% 17.5%;
    --muted-foreground: 215 20.2% 65.1%;
    --accent: 217.2 32.6% 17.5%;
    --accent-foreground: 210 40% 98%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 210 40% 98%;
    --border: 217.2 32.6% 17.5%;
    --input: 217.2 32.6% 17.5%;
    --ring: 224.3 76.3% 94.1%;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
  }
}

/* Custom styles for presentation */
.presentation-slide {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  position: relative;
  overflow: hidden;
}

.slide-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.slide-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.6) 50%, rgba(0,0,0,0.7) 100%);
  z-index: -1;
}

.slide-content {
  position: relative;
  z-index: 1;
  text-align: center;
  max-width: 1200px;
  width: 100%;
}

.text-backdrop {
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(15px);
  border-radius: 20px;
  padding: 3rem 2rem;
  border: 2px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  min-height: fit-content;
}

/* Enhanced text colors for better visibility */
.text-primary {
  color: #ffffff !important;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
}

.text-accent {
  color: #ffffff !important;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
}

.text-secondary {
  color: #ffffff !important;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
}

/* Force all text to be white */
h1, h2, h3, h4, h5, h6, p, span, div {
  color: #ffffff !important;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
}

/* Steve Jobs Style Presentation Layout */
.slide-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 4rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

/* Steve Jobs Style Typography */
.slide-title {
  font-size: 4rem;
  font-weight: 300;
  line-height: 1.1;
  margin-bottom: 2rem;
  letter-spacing: -0.02em;
}

.slide-subtitle {
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.3;
  margin-bottom: 3rem;
  opacity: 0.9;
}

.slide-content-text {
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.6;
  margin-bottom: 2rem;
  max-width: 800px;
}

/* Clean, minimal content areas */
.content-section {
  margin: 3rem 0;
  max-width: 900px;
}

.content-grid {
  display: grid;
  gap: 3rem;
  margin: 2rem 0;
}

.content-grid-2 {
  grid-template-columns: repeat(2, 1fr);
}

.content-grid-3 {
  grid-template-columns: repeat(3, 1fr);
}

.content-grid-4 {
  grid-template-columns: repeat(4, 1fr);
}

/* Clean icon + text combinations */
.icon-text-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.icon-text-group:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-5px);
}

.icon-container {
  margin-bottom: 1.5rem;
}

.icon-text {
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 1.4;
}

/* Grid system for content areas */
.content-grid {
  display: grid;
  gap: 2rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.content-grid-2 {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.content-grid-3 {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.content-grid-4 {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

/* Perfect alignment for icon + text combinations */
.icon-text-group {
  display: grid;
  grid-template-rows: auto auto;
  gap: 1rem;
  place-items: center;
  text-align: center;
  padding: 1.5rem 1rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
}

.icon-container {
  grid-row: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.5rem;
}

.icon-text {
  grid-row: 2;
  text-align: center;
  width: 100%;
}

@media (max-width: 768px) {
  .presentation-slide {
    padding: 1rem;
  }
  
  /* Mobile Steve Jobs Style */
  .slide-content {
    padding: 2rem 1rem;
  }
  
  .slide-title {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
  }
  
  .slide-subtitle {
    font-size: 1.5rem;
    margin-bottom: 2rem;
  }
  
  .slide-content-text {
    font-size: 1.2rem;
    margin-bottom: 1.5rem;
  }
  
  .content-grid {
    gap: 2rem;
  }
  
  .content-grid-2,
  .content-grid-3,
  .content-grid-4 {
    grid-template-columns: 1fr;
  }
  
  .icon-text-group {
    padding: 1.5rem;
  }
  
  .icon-text {
    font-size: 1.1rem;
  }
  
  .content-grid {
    gap: 1.5rem;
    padding: 0.5rem;
  }
  
  /* Mobile text backdrop */
  .text-backdrop {
    padding: 2rem 1.5rem;
    margin: 0.5rem;
    border-radius: 15px;
  }
  
  .content-grid-2 {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .content-grid-3 {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  
  .content-grid-4 {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  
  /* Mobile icon alignment */
  .icon-text-group {
    padding: 1rem 0.5rem;
    gap: 0.75rem;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 12px;
    margin: 0.5rem 0;
  }
  
  .icon-container {
    margin-bottom: 0.25rem;
  }
  
  /* Ensure perfect mobile alignment */
  .flex.flex-col.items-center {
    display: grid !important;
    grid-template-rows: auto auto;
    gap: 0.5rem;
    place-items: center;
    text-align: center;
  }
  
  .flex.items-center {
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
  }
  
  /* Mobile grid fixes */
  .grid.grid-cols-2 {
    display: grid !important;
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .grid.grid-cols-3 {
    display: grid !important;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  
  .grid.grid-cols-4 {
    display: grid !important;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  
  /* Mobile text alignment */
  .flex.flex-col.items-center.space-y-4 span,
  .flex.flex-col.items-center.space-y-4 h3,
  .flex.flex-col.items-center.space-y-4 p {
    text-align: center !important;
    width: 100% !important;
    display: block !important;
  }
}

/* ========================================
   ENHANCED PRESENTATION CONTROLS
   ======================================== */

/* CSS Custom Properties for Presentation */
:root {
  /* Z-index scale */
  --z-background: -1;
  --z-content: 1;
  --z-controls: 30;
  --z-indicators: 30;
  --z-modal: 50;
  
  /* Spacing */
  --controls-bottom: 3rem;
  --controls-gap: 1rem;
  --indicators-top: 1.5rem;
  --indicators-gap: 0.5rem;
  
  /* Animation */
  --transition-fast: 0.2s;
  --transition-normal: 0.3s;
  --transition-slow: 0.5s;
  --ease-smooth: cubic-bezier(0.4, 0, 0.2, 1);
}

/* Enhanced Navigation Controls */
.navigation-controls {
  position: fixed;
  bottom: var(--controls-bottom);
  left: 50%;
  transform: translateX(-50%);
  z-index: var(--z-controls);
  display: flex;
  align-items: center;
  gap: var(--controls-gap);
  will-change: transform;
  backface-visibility: hidden;
  perspective: 1000px;
}

.navigation-controls button {
  contain: layout style paint;
  position: relative;
  overflow: hidden;
  transition: all var(--transition-normal) var(--ease-smooth);
}

.navigation-controls button::before {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(
    circle at center,
    rgba(255, 255, 255, 0.2) 0%,
    transparent 70%
  );
  opacity: 0;
  transition: opacity var(--transition-normal) ease;
  pointer-events: none;
}

.navigation-controls button:hover:not(:disabled)::before {
  opacity: 1;
}

.navigation-controls button:active:not(:disabled) {
  transform: scale(0.95);
}

.navigation-controls button:disabled {
  cursor: not-allowed;
  filter: grayscale(100%);
}

/* Enhanced Slide Indicators */
.slide-indicators {
  position: fixed;
  top: var(--indicators-top);
  left: 50%;
  transform: translateX(-50%);
  z-index: var(--z-indicators);
  display: flex;
  gap: var(--indicators-gap);
  will-change: transform;
  backface-visibility: hidden;
  perspective: 1000px;
}

.slide-indicators button {
  contain: layout style paint;
  position: relative;
  overflow: hidden;
  transition: all var(--transition-normal) var(--ease-smooth);
}

.slide-indicators button::before {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(
    circle at center,
    rgba(255, 255, 255, 0.3) 0%,
    transparent 70%
  );
  opacity: 0;
  transition: opacity var(--transition-fast) ease;
  pointer-events: none;
}

.slide-indicators button:hover::before {
  opacity: 1;
}

.slide-indicators button:active {
  transform: scale(0.9);
}

/* Focus States for Accessibility */
.navigation-controls button:focus-visible,
.slide-indicators button:focus-visible {
  outline: 2px solid #14b8a6;
  outline-offset: 2px;
  border-radius: 9999px;
}

/* Dark Mode Support */
.dark .navigation-controls button:not(:disabled) {
  background: rgba(255, 255, 255, 0.15);
  color: white;
}

.dark .navigation-controls button:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.25);
}

/* High Contrast Mode Support */
@media (prefers-contrast: high) {
  .navigation-controls button,
  .slide-indicators button {
    border: 2px solid currentColor;
  }
}

/* Reduced Motion Support */
@media (prefers-reduced-motion: reduce) {
  .navigation-controls,
  .slide-indicators,
  .navigation-controls button,
  .slide-indicators button {
    transition: none;
  }
  
  .navigation-controls button::before,
  .slide-indicators button::before {
    transition: none;
  }
}

/* Print Mode - Hide Controls */
@media print {
  .navigation-controls,
  .slide-indicators {
    display: none !important;
  }
  
  .presentation-slide {
    page-break-after: always;
  }
}

/* Tablet Responsive (769px - 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  :root {
    --controls-bottom: 2rem;
    --indicators-top: 1.25rem;
  }
}

/* Mobile Responsive (max-width: 768px) */
@media (max-width: 768px) {
  :root {
    --controls-bottom: 1.5rem;
    --indicators-top: 1rem;
    --indicators-gap: 0.25rem;
  }
  
  .navigation-controls {
    gap: 0.5rem;
    padding: 0 1rem;
    max-width: 90vw;
  }
  
  .slide-indicators {
    overflow-x: auto;
    max-width: 90vw;
    scrollbar-width: none; /* Firefox */
    -webkit-overflow-scrolling: touch;
  }
  
  .slide-indicators::-webkit-scrollbar {
    display: none; /* Chrome/Safari */
  }
  
  /* Larger touch targets for mobile accessibility */
  .slide-indicators button {
    min-width: 44px;
    min-height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .navigation-controls button {
    padding: 0.625rem;
  }
}

/* Landscape Mode on Mobile */
@media (max-height: 500px) and (orientation: landscape) {
  .navigation-controls {
    bottom: 1rem;
    scale: 0.9;
  }
  
  .slide-indicators {
    top: 0.75rem;
    scale: 0.85;
  }
}

/* Small Mobile Devices (max-width: 480px) */
@media (max-width: 480px) {
  :root {
    --controls-bottom: 1rem;
    --indicators-top: 0.75rem;
    --indicators-gap: 0.2rem;
  }
  
  .navigation-controls {
    gap: 0.5rem;
  }
  
  .slide-indicators button {
    min-width: 40px;
    min-height: 40px;
  }
}

/* Large Screens - Better spacing */
@media (min-width: 1920px) {
  :root {
    --controls-bottom: 4rem;
    --indicators-top: 2rem;
  }
}
